import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class MapService {

    public configObservable = new Subject<string>();
    public static pos: any = {};
    public static mapPath: any = [];
    public static direction: any = {};

    public emitConfig(val): void {
        this.configObservable.next(val);
    }
    
    public setMapPath(value): void {
        MapService.mapPath = value;             
    }
    
     public getMapPath(): any {        
        return MapService.mapPath;        
    }
    
    public setdirectionsRenderer(value): void {
        MapService.direction = value;             
    }
    
     public getdirectionsRenderer(): any {        
        return MapService.direction;        
    }

    public setPosition(v): void {
        MapService.pos = v;             
    }
    
     public getPosition(): any {        
        return MapService.pos;        
    }

}


