import { Component, ViewEncapsulation, NgModule, ViewChild, ElementRef, OnInit, forwardRef } from '@angular/core';
import {BrowserModule} from '@angular/platform-browser'
import { Router } from '@angular/router';
import { FormGroup, FormControl, AbstractControl, FormBuilder, Validators} from '@angular/forms';
import { HistoryService } from './history.service';
import { ToastrService } from 'ngx-toastr';
import { MapService } from '../../pages.mapservice'

@Component({
    selector: 'az-layouts',
    encapsulation: ViewEncapsulation.None,
    templateUrl: './history.component.html',
    styleUrls: ['../../myapps/myapps.component.css'],
    providers: [HistoryService, ToastrService, MapService]

})
export class HistoryComponent implements OnInit {
    @ViewChild('closeBtn') closeBtn: ElementRef;
    public router: Router;
    public vehicleHistory: FormGroup;
    public historyService: HistoryService;
    public toastrService: ToastrService;
    public device_id: AbstractControl;
    public start: AbstractControl;
    public end: AbstractControl;
    public responseJson: any = {};
    public vehicleJson: any = [];

    public latlngitude = "18.571635, 73.7707283";
    public zoom: number = 16;
    public mapPath: any = [];

    constructor(router: Router, fb: FormBuilder, historyService: HistoryService, toastrService: ToastrService, private mapService: MapService) {
        this.router = router;
        this.historyService = historyService;
        this.toastrService = toastrService;
        this.vehicleHistory = fb.group({
            'device_id': ['', Validators.required],
            'start': ['', Validators.required],
            'end': ['', Validators.required]
        });

        this.device_id = this.vehicleHistory.controls['device_id'];
        this.start = this.vehicleHistory.controls['start'];
        this.end = this.vehicleHistory.controls['end'];
        this.vehicleHistory.controls['device_id'].setValue('0');
        this.vehicleHistory.controls['start'].setValue('2017-08-14T00:00:00');
        this.vehicleHistory.controls['end'].setValue('2017-08-20T23:59:00');
    }

    ngOnInit() {
        this.getVehicle();
    }
    
    public getVehicle() {
        this.historyService.getVehicle().subscribe(
            data => {
                this.vehicleJson = data["fleet_status"];
            }
        );
    }

    public onSubmit(values: Object): void {
        if (this.vehicleHistory.valid) {
            this.historyService.getHistory(values).subscribe(
                data => {
                    this.responseJson = data;
                    if (data["flag"] == "0") {
                        this.toastrService.error('No Data Found');
                    } else {
                        this.setMapPath();
                    }
                }
            );
        }
    }

    public setMapPath() {
        this.zoom = 18;
        this.mapPath = [];
        this.responseJson.forEach((item, index) => {
            let latObject: any = {};
            latObject["lat"] = Number(item.Latitude);
            latObject["lng"] = Number(item.Longitude);
            this.mapPath.push(latObject);            
        });
        this.mapService.setMapPath(this.mapPath);
    }

}




